import { BackProvider } from './../../providers/back/back';
import { ToastController, LoadingController } from 'ionic-angular';
import { Component, Input, Output, EventEmitter } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators';

@Component({
  selector: 'file-upload',
  templateUrl: 'file-upload.html'
})
export class FileUploadComponent {

  @Input() text: string = 'Selecciona una imágen'
  @Input() upload: string = 'Subiendo imágen...'
  @Input() error: string = 'Ocurrió un error al subir la imágen'
  @Input() success: string = 'Modificar o agregar otra foto del comprobante'
  @Input() clear: boolean = true
  @Input() showPreview: boolean = true
  @Input() imageWidth: string = '40%'

  @Output() onFinish = new EventEmitter<any>()

  private loader: boolean = false
  private resImage: string

  constructor(
    private toast: ToastController,
    private load: LoadingController,
    private back: BackProvider,
    private storage: AngularFireStorage
  ) {}

  uploadFile(filename: string, file: File): Promise<string | null> {
    return new Promise((resolve, reject) => {
      const ref = this.storage.ref(`pagos/${filename}`)
      const task = ref.put(file)
      task.snapshotChanges().pipe(
        finalize(() => {
          ref.getDownloadURL().subscribe(url => resolve(url))
        })
      ).subscribe()
    })
  }

  async onImageSelected(files: File[]) {

    if (!files) return
    if (files.length <= 0) return

    const l = this.load.create({content: 'Subiendo imágen...'})
    l.present()
    this.loader = true
    this.text = this.upload

    const url = await this.uploadFile(files[0].name, files[0])
    this.text = this.success
    this.loader = false
    l.dismiss()
    this.resImage = url
    this.onFinish.emit(url)
    
  }

}



