import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController, LoadingController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { Collections } from '../../app/app.models';
import { take } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-pago-detail',
  templateUrl: 'pago-detail.html',
})
export class PagoDetailPage {

  public pago: any = this.navParams.get('pago')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private toast: ToastController,
    private load: LoadingController,
    public back: BackProvider,
    public auth: AuthProvider,
    private afs: AngularFirestore
    ) {
  }

  ionViewDidLoad() {
    if (!this.pago) this.viewCtrl.dismiss()
    console.log(this.pago);
    
  }

  async postComment(text: string) {

    if (!text) return
    if (text.length <= 0) return

    const l = this.load.create({content: 'Agregando comentario...'})
    l.present()

    // Restar cantidad a control
    if (!this.pago.restado) {
      
    }

    if (!this.pago.notas) this.pago.notas = []
    this.pago.notas.push({text, createdAt: new Date()})

    await this.afs.collection(Collections.ZAMNA_PAGO).doc(this.pago.id).update({notas: this.pago.notas})
    l.dismiss()
    this.pago.lastComment = ''

  }

  async addImage(url: string) {

    if (!url) return
    const l = this.load.create({content: 'Agregando imágen...'})
    l.present()
    
    if (!this.pago.imgs) this.pago.imgs = []
    this.pago.imgs.push({url, createdAt: new Date()})

    this.pago.pago.status = 'waiting'

    await this.afs.collection(Collections.ZAMNA_PAGO).doc(this.pago.id).update({imgs: this.pago.imgs, pago: this.pago.pago})
    l.dismiss()

  }

  openImage(url: string) {
    if (!this.auth.isAdmin) return
    let w = window.open(url, '_blank')
    w.focus()
  }

  async updateStatus(status: string) {

    const l = this.load.create({content: 'Cambiando status...'})
    l.present()
 
    this.pago.pago.status = status

    await this.afs.collection(Collections.ZAMNA_PAGO).doc(this.pago.id).update({pago: this.pago.pago})
    l.dismiss()
    this.toast.create({message: 'Status cambiado correctamente.', duration: 2000}).present()

    if (!this.pago.pago.restado && status == 'completed') {
      const control = await this.afs.collection(Collections.CONTROL_PAGO).doc<any>(this.pago.control).valueChanges().pipe(take(1)).toPromise()
      if (control) {
        await this.afs.collection(Collections.CONTROL_PAGO).doc(this.pago.control).update({amountLeft: control.amountLeft - this.pago.amount})
        this.pago.pago.restado = true
        await this.afs.collection(Collections.ZAMNA_PAGO).doc(this.pago.id).update({pago: this.pago.pago})
      }
    }

  }

}



